<template>
  <div class="AnswerCard">
    <div class="box">
      <div class="left">
        <subjcet />
      </div>
      <div class="right">
        <div class="imgBox">
          <img src="../../../../static/images/作业列表.png" alt="" />
        </div>
        <div class="table">
          <table>
            <thead>
              <tr style="background-color:#f8f8f8;">
                <td style="color:#fb8b22;font-size:16px;padding:5px">序号</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">
                  作业名称
                </td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">题型</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">
                  作业类型
                </td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">来源</td>
                <td style="color:#fb8b22;font-size:16px;padding:5px">时间</td>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in list"
                :key="index"
                style="margin-bottom:5px"
                class="trHover"
              >
                <td style="padding:10px;font-size:16px">{{ index + 1 }}</td>
                <td style="padding:10px;font-size:16px" @click="showBox">
                  <a href="javascript:void(0);">内能</a>
                </td>
                <td style="padding:10px;font-size:16px">结构化</td>
                <td style="padding:10px;font-size:16px">巩固</td>
                <td style="padding:10px;font-size:16px">陈老师</td>
                <td style="padding:10px;font-size:16px">2020-01-01</td>
              </tr>
            </tbody>
          </table>
          <el-pagination
            layout="prev, pager, next"
            :total="100"
            class="page"
            style="color: #2ab8b3;"
          ></el-pagination>
        </div>
        <div class="wrongList" v-if="show">
          <div class="listBox" v-for="(item, index) in list" :key="index">
            <div class="listNumber">
              <span class="daoru">导入</span>
              <span style="color:#ffac67">编码:441144578</span>
            </div>
            <div class="listBody">
              <img src="../../../../static/images/testbody.png" alt="" />
            </div>
            <div class="listBottom">
              <span>使用次数：25次</span>
              <span style="margin-left:33px">作答人数：255人</span>
              <el-button>+添加备用习题</el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script></script>
<script>
import subjcet from "@/components/grade/subject.vue";
export default {
  components: {
    subjcet
  },
  data() {
    return {
      list: [1, 1, 1, 1, 1, 1, 1],
      show: false
    };
  },
  methods: {
    showBox() {
      this.show = true;
    }
  }
};
</script>
<style lang="less">
.AnswerCard {
  .box {
    width: 100%;
    display: flex;
    justify-content: space-between;
    background-color: #f8f8f8;
    position: relative;
    .left {
      width: 18%;
      margin-right: 10px;
    }
    .right {
      background-color: #fff;
      flex: 1;
      .wrongList {
        background-color: #fff;
        border-top: 10px solid #eee;
        padding: 30px;

        .listBox {
          border: 1px solid #2bb7b3;
          margin-bottom: 20px;
          .listNumber {
            padding-top: 3px;
            box-sizing: border-box;
            height: 29px;
            border-bottom: 1px solid #eee;
            .daoru {
              background-color: #ff8a00;
              box-shadow: 3px 3px 0px 0px #ffdcb2, 3px 3px 0px 0px #ffdcb2;
              color: #fff;
              padding: 3px;
            }
          }
          .listBottom {
            height: 56px;
            background-color: #f5f5f5;
            line-height: 56px;
            padding: 0px 24px;
            button {
              margin-left: 55%;
              border: 0;
              background-color: #2bb7b3;
              color: #fff;
            }
          }
        }
      }
      .imgBox {
        img {
          margin-top: 10px;
        }
      }
      .table {
        width: 100%;
        //  height: 50%;
        margin: 0 auto;
        text-align: center;
        // border: 1px solid #2ab8b3;
        //  margin-bottom: 15px;
        border-bottom: 10px solid #f8f8f8;
        table {
          width: 100%;
          height: 100%;
          // text-align: center;
          padding: 18px;
          .trHover:hover {
            background-color: #faf1ed;
          }
        }
        .page {
          position: relative;
          margin: 5px;
          right: -318px;
          bottom: 0;
        }
      }
    }
  }
}
</style>



